table{
width: 100%;
border-spacing: 0px 20px;
font-family: Roboto;
font-style: normal;
font-weight: bold;
font-size: 24px;
line-height: 28px;

color: #000000;

}

tr{ 
height: 80px;

box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
border-radius: 20px;
}

th{
    border-right: 2px solid #fff;
    background-color: #2196F3;
    border-radius: 20px;

}

th:last-child {
  border-right: none;
}


td{
    padding-left: 23px;
    text-align: left;
    border-right: 2px solid #b2b2b2;

    cursor:pointer;
}

td:first-child{
    padding-left: 0px;
    text-align:center;
    cursor:text;

}



td:last-child{
    border-right: none;
    cursor:text;
}
